import { AsyncStorage } from 'react-native';
import { SessionStore } from '@phinxlab/libby-core-public';

export class SessionStoreMobile extends SessionStore {

    async retrieve() {
        console.log("Retrieve()");
        try {
            const token = await AsyncStorage.getItem('x-chino-token');
            return token;
        } catch (error) {
            console.log(error);
            return false;
        }
    }

    async clean() {
        console.log("Clean()");
        try {
            await AsyncStorage.removeItem('x-chino-token');
        } catch (error) {
            return false;
        }
    }

    async persist(token) {
        console.log("Persist()");
        try {
            await AsyncStorage.setItem('x-chino-token', token);    
        } catch (error) {
            console.log(error);
            return false;
        }
    }

    static create() {
        return new SessionStoreMobile();
    }
}